<template>
  <div>
    <!-- banner -->
    <div class="archive-banner banner">
      <h1 class="banner-title"></h1>
    </div>
    <!--  -->
    <v-card class="blog-container">
      <timeline>
        <timeline-title> {{ count }} </timeline-title>
        <timeline-item v-for="item of archiveList" :key="item.id">
          <!--  -->
          <span class="time">{{ item.createTime | date }}</span>
          <!--  -->
          <router-link
            :to="'/articles/' + item.id"
            style="color:#666;text-decoration: none"
          >
            {{ item.title }}
          </router-link>
        </timeline-item>
      </timeline>
      <!--  -->
      <v-pagination
        v-model="pageNum"
        :length="Math.ceil(count / 10)"
        total-visible="7"
      />
    </v-card>
  </div>
</template>

<script>
import { Timeline, TimelineItem, TimelineTitle } from "vue-cute-timeline";
import { getArchives } from "@/api/archive";
export default {
  created() {
    this.listArchives();
  },
  components: {
    Timeline,
    TimelineItem,
    TimelineTitle
  },
  data: function() {
    return {
      pageNum: 1,
      count: 0,
      archiveList: []
    };
  },
  methods: {
    listArchives() {
      getArchives(this.pageNum).then(res => {
        this.count = res.data.count;
        this.archiveList = res.data.archiveList.list;
      });
    }
  },
  watch: {
    pageNum(value) {
      getArchives(value).then(res => {
        this.count = res.data.count;
        this.archiveList = res.data.archiveList.list;
      });
    }
  }
};
</script>
<style scoped>
.archive-banner {
  background: #49b1f5 no-repeat center center;
}
.time {
  font-size: 0.75rem;
  color: #555;
  margin-right: 1rem;
}
</style>
