<template>
  <div class="reply-input-wrapper" style="display: none" ref="reply">
    <textarea
      class="comment-textarea"
      :placeholder="' @' + nickname + ''"
      auto-grow
      dense
      v-model="commentForm.content"
    />
    <div class="emoji-container">
      <div style="margin-left:auto">
        <button @click="cancleReply" class="cancle-btn v-comment-btn">
          
        </button>
        <button @click="insertReply" class="upload-btn v-comment-btn">
          
        </button>
      </div>
    </div>
    <v-form>
      <v-container>
        <v-row>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-col cols="12" md="4">
                <v-text-field
                  v-model="commentForm.nickname"
                  label=""
                  required
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </v-col>
            </template>
            <span>QQ</span>
          </v-tooltip>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-col cols="12" md="4">
                <v-text-field
                  v-model="commentForm.email"
                  label=""
                  required
                  v-bind="attrs"
                  v-on="on"
                ></v-text-field>
              </v-col>
            </template>
            <span></span>
          </v-tooltip>
          <v-switch
            style="padding-left: 30px !important;"
            v-model="commentForm.notice"
            :label="''"
          ></v-switch>
        </v-row>
      </v-container>
    </v-form>
  </div>
</template>

<script>
import { submitComment } from "@/api/comment";
export default {
  data: function() {
    return {
      index: 0,
      nickname: "",
      parentId: null,
      commentForm: {
        content: "",
        nickname: "",
        email: "",
        notice: false
      }
    };
  },
  methods: {
    cancleReply() {
      this.$refs.reply.style.display = "none";
    },
    insertReply() {
      //
      if (this.commentForm.content.trim() === "") {
        this.$toast({ type: "error", message: "" });
        return false;
      }
      if (this.commentForm.nickname.trim() === "") {
        this.$toast({ type: "error", message: "" });
        return false;
      }
      if (this.commentForm.email.trim() === "") {
        this.$toast({ type: "error", message: "" });
        return false;
      }
      //
      var reg = /^[A-Za-z0-9\u4e00-\u9fa5]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      if (
        this.commentForm.email.trim() !== "" &&
        !reg.test(this.commentForm.email)
      ) {
        this.$toast({ type: "error", message: "" });
        return false;
      }
      const path = this.$route.path;
      const arr = path.split("/");
      let token = window.localStorage.getItem("adminToken");
      let form = {
        nickname: this.commentForm.nickname,
        content: this.commentForm.content,
        email: this.commentForm.email,
        notice: this.commentForm.notice,
        blogId: arr[2],
        parentCommentId: this.parentId,
        page: 0
      };
      submitComment(token, form).then(res => {
        if (res.code === 200) {
          this.$emit("reloadReply", this.index);
          this.commentForm.content = "";
          this.$refs.reply.style.display = "none";
          this.$toast({ type: "success", message: "" });
        } else {
          this.$toast({ type: "success", message: res.data.message });
        }
      });
    }
  }
};
</script>

<style scoped>
.reply-input-wrapper {
  border: 1px solid #90939950;
  border-radius: 4px;
  padding: 10px;
  margin: 0 0 10px;
}
</style>
